.dialogs{
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background: #fff;
    border-radius: 4px;
    box-shadow: 0 1px 0 0 #d3d9de, 0 0 0 1px #e7e8ec;
    margin: 15px 15px 0 0;
}
.dialogsItems{
    padding: 10px;
    width: 30%;
    border: solid 2px #ccc;
}
.dialogsItems .active{
    color: gold;
}
.messages{
    padding: 10px;
    width: 100%;
}
.messages .message{
    color: #333;
}

.dialogsItems a{
    color: #333;
    text-decoration: none;
}